(function () {
  'use strict';

  angular
    .module('preziPresentio')
    .controller('SearchResultViewController', SearchResultViewController);

  /** @ngInject */
  function SearchResultViewController(PresentationService, _, $stateParams, $log, $state) {
    var vm = this;
    vm.query = $stateParams.query;

    function _setupPresentationTiles(presentations) {
      vm.presentationTiles = _.map(presentations, function (presentation) {
        presentation['span'] = {
          default: 2
        };
        return presentation;
      });
    }

    var onSuccess = function (response) {
      vm.shouldShowErrorView = false;
      vm.shouldShowProgressView = false;
      vm.showSearchedByText = true;
      _setupPresentationTiles(response.presentations);
    };

    var onFailure = function (response) {
      vm.shouldShowProgressView = false;
      vm.errorMessage = 'Something bad happened. Please refresh.';
      vm.shouldShowErrorView = true;
      $log.debug(response);
    };

    function _init_() {
      vm.shouldShowProgressView = true;
      vm.showSearchedByText = false;
      PresentationService.getMatchedPresentations(vm.query)
        .success(onSuccess)
        .error(onFailure);
    }

    _init_();

    vm.close = function () {
      $state.go('home'); // Go to parent state
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('preziPresentio')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(PresentationService, _, $log, $state) {
    var vm = this;
    vm.orderByOption = {};
    vm.orderBy = {
      options: [
        {
          name: 'Latest first',
          value: 'desc'
        },
        {
          name: 'Older first',
          value: 'asc'
        }
      ]
    };

    vm.visiblePagination = {
      lower: vm.currentOffset + 1,
      upper: vm.currentOffset + 1 + vm.pageSize
    };

    function _setupPresentationTiles(presentations) {
      vm.presentationTiles = _.map(presentations, function (presentation) {
        presentation['span'] = {
          default: 2
        };
        return presentation;
      });
      vm.orderByOption = vm.orderBy.options[0].value;
      vm.orderPresentationsBy(vm.orderByOption);
    }

    var onSuccess = function (response) {
      vm.shouldShowProgressView = false;
      vm.totalPresentations = response.total;
      vm.visiblePagination.lower = (vm.currentOffset * vm.pageSize) + 1;
      vm.visiblePagination.upper = vm.visiblePagination.lower + vm.pageSize - 1;
      if (!vm.maxOffset) {
        vm.maxOffset = Math.ceil(vm.totalPresentations / vm.pageSize) - 1;
      }
      _setupPresentationTiles(response.presentations);
    };

    var onFailure = function (response) {
      vm.shouldShowProgressView = false;
      vm.errorMessage = 'Something bad happened. Please refresh.';
      $log.debug(response);
    };

    function _init_(offset) {
      vm.query = '';
      vm.errorMessage = '';
      vm.shouldShowProgressView = true;
      vm.searchPlaceholder = 'Search all presentations';
      vm.currentOffset = offset;
      vm.pageSize = 20;
      vm.totalPresentations = 0;

      PresentationService.getPresentations(vm.currentOffset, vm.pageSize)
        .success(onSuccess)
        .error(onFailure);
    }

    vm.fetchNextPage = function () {
      vm.currentOffset += 1;
      _init_(vm.currentOffset);
    };

    vm.fetchPrevPage = function () {
      vm.currentOffset -= 1;
      vm.currentOffset = vm.currentOffset < 0 ? 0 : vm.currentOffset;
      _init_(vm.currentOffset);
    };

    vm.searchByTitle = function () {
      $state.go('search', {query: vm.query});
    };

    vm.orderPresentationsBy = function (orderByOption) {
      var objectsWithDateFromString = _.map(vm.presentationTiles, function (presentation) {
        presentation['createdAtDateObj'] = Date.parse(presentation['createdAt']);
        return presentation;
      });
      vm.presentationTiles = _.orderBy(objectsWithDateFromString, ['createdAtDateObj'], [orderByOption]);
    };

    _init_(0);
  }
})();

(function () {
  'use strict';

  angular
    .module('preziPresentio.directives')
    .directive('progressIndicatorDirective', ProgressIndicatorDirective);

  /** @ngInject */
  function ProgressIndicatorDirective() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/components/progress.indicator.directive/progress_indicator.html',
      scope: {
        shouldIndicateProgress: '=',
        optionalMessage: '='
      }
    };
  }
})();

(function () {
  'use strict';

  angular.
    module('preziPresentio.directives')
    .directive('onEnterPress', OnEnterPressDirective);

  function OnEnterPressDirective() {
    return {
      link: function (scope, element, attrs) {
        element.bind("keydown keypress", function (event) {
          if (event.which === 13) {
            scope.$apply(function () {
              scope.$eval(attrs.onEnterPress);
            });

            event.preventDefault();
          }
        });
      }
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('preziPresentio.directives')
    .directive('alertMessageDirective', AlertMessageDirective);

  /** @ngInject */
  function AlertMessageDirective() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/components/alert.message.directive/alert_message.html',
      scope: {
        errorMessage: '='
      }
    };
  }
})();

(function () {
  'use strict';
  angular.
    module('lodash').factory('_', LodashFactory);

  LodashFactory.$inject = ['$window'];

  /** @ngInject */
  function LodashFactory($window) {
    return $window._;
  }
})();

(function () {
  'use strict';
  angular.
    module('moment').factory('moment', MomentFactory);

  MomentFactory.$inject = ['$window'];

  /** @ngInject */
  function MomentFactory($window) {
    return $window.moment;
  }
})();

(function () {
  'use strict';

  angular
    .module('preziPresentio.services')
    .factory('PresentationService', PresentationService);

  /** @ngInject */
  function PresentationService($http) {
    var BASE_URL = 'https://prezipresentioserver.herokuapp.com/api/presentation/all';
    return {
      getPresentations: function (pageOffset, pageSize) {
        return $http.get(BASE_URL + '/' + pageOffset + '/' + pageSize);
      },
      getMatchedPresentations: function (query) {
        return $http.get(BASE_URL + '?q=' + query);
      }
    };
  }
})();


